<!--  -->
<template>
  <div id="app">
    <div class="top-bg"></div>
    <div class="flex-cloum main">
      <el-form
        :model="form"
        :rules="rules"
        ref="formRef"
        class="flex-cloum-align-center"
        @submit.native.prevent
      >
        <!-- 同时头像 -->
        <el-upload
          action
          :show-file-list="false"
          :before-upload="beforeFileUpload"
          :http-request="logoSelectCallBack"
          :disabled="!allowedEdit"
        >
          <img v-if="form.logoUrl" class="logo" :src="form.logoUrl" />
          <div
            v-else
            class="upload-tips-div flex-cloum-align-center"
            :class="notAllowedClassObject"
          >
            <span class="tips1">上传公司logo</span>
            <span class="tips2">尺寸200px*200px，格式jpg， 分辨率72像素/英寸，不大于 3MB</span>
          </div>
        </el-upload>

        <div class="flex-row company-div">
          <!-- 公司名称 -->
          <el-form-item class="flex-row-align-center" label="公司名称" prop="email" label-width="200">
            <div class="flex-row-align-center">
              <el-input :disabled="!allowedEdit" v-model="form.company_name" placeholder="请填写公司名称"></el-input>
              <span class="tips1" @click="goVerifyPageAction()">去认证</span>
            </div>
          </el-form-item>

          <el-form-item class="flex-row-align-center" label="合作邮箱" prop="email" label-width="200">
            <el-input :disabled="!allowedEdit" v-model="form.email" placeholder="发布后，更改需联系客服"></el-input>
          </el-form-item>
        </div>

        <el-form-item style="margin-top:17px; width: 100%;" prop="desc">
          <el-input
            :disabled="!allowedEdit"
            class="el-input-textarea"
            maxlength="300"
            show-word-limit
            type="textarea"
            placeholder="请填写公司简介"
            v-model="form.desc"
          ></el-input>
        </el-form-item>

        <div class="title-tips">代表作品</div>
        <div class="addlist flex-cloum">
          <el-form-item
            v-for="(item, index) in form.works"
            :key="index"
            :prop="'works.' + index + '.work'"
            :rules="{required: true,
                    message: '请填写完代表作', trigger: 'blur'
                }"
          >
            <el-input :disabled="!allowedEdit" placeholder="请填写代表作" v-model="item.work" clearable></el-input>
          </el-form-item>

          <div class="add-btn flex-row-align-center" @click="addInputAction">
            <i class="el-icon-plus add-icon"></i>
            <span>新增</span>
          </div>
        </div>

        <div class="btns margin-center">
          <!-- <el-button class="preview" @click="previewAction">预览</el-button> -->
          <el-button class="release" @click="onSubmit">完成</el-button>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
import qiniuApi from "utils/qiniu.js";

export default {
  name: "Company",
  data() {
    return {
      allowedEdit: false, // 是否允许编辑
      form: {
        company_name: "莉莉丝科技（上海）有限公司",
        email: "",
        logoUrl: "",
        desc: "",
        works: [{ work: "" }]
      },
      rules: {
        email: [
          {
            required: true,
            type: "email",
            message: "请输入正确的邮箱地址",
            trigger: ["blur", "change"]
          }
        ],
        desc: [{ required: true, message: "请填写公司简介", trigger: ["blur"] }]
      }
    };
  },
  computed: {
    notAllowedClassObject() {
      return {
        notAllowed: !this.allowedEdit
      };
    }
  },
  //生命周期 - 创建完成（访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（访问DOM元素）
  mounted() {},
  //方法
  methods: {
    // 去认证
    goVerifyPageAction() {
      // if (this.verifyStyle.text === "已认证") {
      //   // 查看认证
      //   this.$router.push({ path: "/account/info" });
      // } else {
      //   // 修改认证
      //   this.$router.push({ path: "/account/Verify" });
      // }
      this.$router.push({ path: "/account/Verify" });
    },

    // 添加input输入框
    addInputAction() {
      let works = this.form.works;
      let s = "works." + (works.length - 1) + ".work";
      this.$refs.formRef.validateField(s, error => {
        console.log("error ===>", error);
        if (!error) {
          this.form.works.push({ work: "" });
        }
      });
    },

    logoSelectCallBack(res) {
      // this.qiniu_upload(res, "");
      this.form.logoUrl = URL.createObjectURL(res.file);
      // if (res != this.imageRes.license_img_res) {
      //   this.imageRes.license_img_res = res;
      //   this.form.license_img = "";
      // }
    },
    qiniu_upload(req, imagekey) {
      return new Promise((resolve, reject) => {
        // 如果已经上传过，就不用上传
        if (imagekey) {
          return resolve(imagekey);
        }
        qiniuApi.upload(
          req,
          next => {},
          error => {
            reject(error);
          },
          complete => {
            // console.error(complete);
            resolve(complete.key);
          }
        );
      });
    },

    beforeFileUpload(file) {
      const isJPG = file.type === "image/jpeg" || file.type === "image/png";
      const isLt2M = file.size / 1024 / 1024 <= 3;

      if (!isJPG) {
        this.$message.error("上传企业营业执照图片格式不正确!");
      }
      if (!isLt2M) {
        this.$message.error("上传企业营业执照大小不能超过3MB!");
      }
      return isJPG && isLt2M;
    },
    // 发布
    onSubmit() {
      this.$refs.formRef.validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  },
  //引入的组件
  components: {}
};
</script>


